*{
  font-family: 'Lato', sans-serif;
  outline: none;
  box-sizing: border-box;
}
body{
  background-color: #202225;
}
.disabledButton{
  cursor:not-allowed;
}
.button{
  cursor:pointer;
}
#inputDiv{
  background-color: #3c3f44;
  width:300px;
  height:50px;
  border:none;
  position: relative;
  display: block;
  border-radius: 8px;
  padding:0 0 0 10px;
  font-size:16px;
  transition: 0.08s all linear;
  color:#fff;
  margin:0 20px 20px 20px;
}
#inputDiv img{
  position: relative;
  display: inline-block;
  width:30px;
  left:0;
  margin:0;
  vertical-align: middle;
}
#shareIcon{
  display: inline-block;
  position: relative;
  height:40px;
  vertical-align: text-bottom;
  cursor: pointer;
}
input{
  background-color: transparent;
  height:100%;
  position: relative;
  left:0;
  top:0;
  border:none;
  color:#fff;
  width:calc(100% - 40px);

}
.focusedDiv {
  background-color: #40444b !important;
}

.title1{
  font-weight:700;
  font-size:45px;
  color:#fff;
  margin:20px;
  text-align: left;
  display: inline-block;
}

.title2{
  font-weight:700;
  font-size:25px;
  color:#fff;
  text-align: left;
  margin:0 0 20px 0;
}
.regularText{
  font-weight: 400;
  font-size:16px;
  color:#fff;
}

#topBanner{
  position:relative;
  top:0;
  left:0;
  margin:0;
  width:100%;
  height:auto;
  background-color: #a83232;
  padding:10px 20px;
}
#topBanner p{
  margin:0;
}


#nominationsMainContainer{
  position: fixed;
  top:40px;
  right:0;
  width:300px;
  height:calc(100% - 40px);
  padding:20px;
  overflow-y: auto;
  /* background-color: #2f3136; */
}
.nominationCardPoster{
  width:100%;
  height:150px;
  border-radius: 8px;
  position: relative;
  display: block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin:0 0 20px 0;
}
.nominationCardPoster::before{
  content:"";
  position: absolute;
  top:0;
  left:0;
  height:100%;
  width:100%;
  border-radius: 8px;
background: linear-gradient(0deg, rgba(0,0,0,0.9584427521008403) 0%, rgba(0,0,0,0.6167060574229692) 39%, rgba(0,0,0,0) 57%);
}
.removeNominationButton{
  position:absolute;
  top:-10px;
  left:-10px;
  background-color: #fff;
  height:30px;
  width:30px;
  border-radius: 50%;
  cursor: pointer;
}
.removeNominationButton img{
  width:80%;
  position: absolute;
  left:10%;
  top:50%;
  transform: translateY(-50%);
}
.nominationCardPoster p{
  position: absolute;
  bottom:10px;
  left:10px;
  text-align: left;
  margin:0;
}
#contentBody{
  position: relative;
  top:0;
  left:0;
  width:100%;
  height:auto;
  padding:0px 0 0 0;
  text-align: left;
}


#searchResultsMainContainer{
  width:calc(100% - 300px);
  left:0;
  position: relative;
  top:0;
  padding:0 0 40px 20px;
  height: auto;
  min-height: 600px;
}

#shareLink{
  position: absolute;
  bottom:0px;
  left:20px;
  width:100%;
  color:#fff;
  text-align: left;
}
.middleText{
  position: absolute;
  top:30%;

  left:50%;
  transform: translateY(-50%) translateX(-50%);

}
